/** Media Queries **/
html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
}

p {
  color: #000;
  font-size: 2rem;
  line-height: 2;
}

.contenedor {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

a {
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

h1, h2, h3 {
  margin: 0 0 2rem 0;
}

h1 {
  font-size: 4rem;
}

h3 {
  font-size: 2.8rem;
  text-align: center;
  text-transform: uppercase;
}

ul {
  padding: 0;
  list-style: none;
}

.site_header {
  background-image: linear-gradient(rgba(57, 70, 89, 0.7) 0%, rgba(57, 70, 89, 0.7) 100%), url("../img/header.webp");
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: 10rem;
  padding-bottom: 10rem;
}
.site_header .header-grid {
  display: grid;
  grid-template-columns: 1fr;
  grid-template-rows: repeat(2, 1fr);
  gap: 5rem;
}
@media (min-width: 768px) {
  .site_header .header-grid {
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: repeat(2, 1fr);
  }
}
.site_header .header-grid .logo {
  width: 20rem;
  height: 10rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .site_header .header-grid .logo {
    margin: 0;
  }
}
.site_header .header-grid .contenido-header {
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.site_header .header-grid .contenido-header h1 {
  color: white;
  width: 90%;
  padding-bottom: 2rem;
  text-align: center;
}
.site_header .header-grid .contenido-header .btn {
  background-color: #84d9bc;
  padding: 1rem 3rem;
  color: #313c4e;
  font-weight: bold;
  text-align: center;
}
@media (min-width: 768px) {
  .site_header .header-grid .contenido-header {
    display: block;
  }
  .site_header .header-grid .contenido-header h1 {
    color: white;
    width: 30%;
    padding-bottom: 2rem;
    text-align: left;
  }
  .site_header .header-grid .contenido-header .btn {
    background-color: #84d9bc;
    padding: 1rem 3rem;
    color: #313c4e;
    font-weight: bold;
  }
}

.crea-grid {
  padding-top: 5rem;
  display: grid;
  grid-template-rows: repeat(2, 1fr);
  row-gap: 2rem;
}
@media (min-width: 768px) {
  .crea-grid {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    row-gap: 2rem;
    align-items: center;
  }
}

.crea-grid .contenido h2 {
  text-align: center;
  border-bottom: 5px solid #84d9bc;
  display: block;
}
@media (min-width: 768px) {
  .crea-grid .contenido h2 {
    text-align: left;
    border-bottom: 5px solid #84d9bc;
    display: inline;
  }
}
.crea-grid .contenido p {
  text-align: center;
}
@media (min-width: 768px) {
  .crea-grid .contenido p {
    text-align: left;
  }
}

.beneficios {
  padding-top: 5rem;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .beneficios .grid-beneficios {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: 1fr;
    justify-items: center;
    gap: 5rem;
  }
}
.beneficios .grid-beneficios .contenido-beneficios h2 {
  color: #FFFFFF;
  text-align: center;
}
.beneficios .grid-beneficios .contenido-beneficios p {
  padding: 0 3rem;
  color: #FFFFFF;
  text-align: justify;
}
@media (min-width: 768px) {
  .beneficios .grid-beneficios .contenido-beneficios h2 {
    text-align: left;
    border-bottom: 5px solid #FFFFFF;
    display: inline;
  }
  .beneficios .grid-beneficios .contenido-beneficios p {
    padding: 0;
  }
}
.beneficios .grid-beneficios .imagen {
  width: 100%;
}
@media (min-width: 768px) {
  .beneficios .grid-beneficios .imagen {
    width: 60%;
  }
  .beneficios .grid-beneficios .imagen img {
    transition: all 0.3s ease;
  }
  .beneficios .grid-beneficios .imagen img:hover {
    transform: scale(1.2) rotate(3deg);
  }
}

.estadistica {
  padding-top: 5rem;
  padding-bottom: 5rem;
}
.estadistica h2 {
  border-bottom: 5px solid #84d9bc;
  display: block;
  text-align: center;
}
@media (min-width: 768px) {
  .estadistica h2 {
    display: inline;
  }
}
.estadistica p {
  text-align: justify;
  margin: auto 1rem auto 1rem;
}
@media (min-width: 768px) {
  .estadistica p {
    margin: auto;
  }
}

.planes {
  background-color: #313c4e;
  padding: 5rem 1rem;
}
@media (min-width: 768px) {
  .planes {
    padding: 5rem auto;
  }
}
.planes h2 {
  color: #FFFFFF;
  border-bottom: 5px solid #FFFFFF;
  display: block;
}
@media (min-width: 768px) {
  .planes h2 {
    display: inline;
  }
}
.planes p {
  color: #FFFFFF;
}

.nuestros-planes {
  display: flex;
  flex-direction: column;
  gap: 2.5rem;
  margin: 0 auto;
  max-width: 100%;
}
@media (min-width: 768px) {
  .nuestros-planes {
    flex-direction: row;
    gap: 0;
    max-width: 80rem;
    margin: 10rem auto 0 auto;
  }
}
.nuestros-planes .plan {
  flex-grow: 1;
  padding: 16rem 4rem 4rem 4rem;
  background-color: #FFFFFF;
}
.nuestros-planes .plan.inicio {
  background-image: url("../img/icono_inicio.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .nuestros-planes .plan.inicio {
    border-radius: 2rem;
  }
}
.nuestros-planes .plan.inicio:hover {
  animation-name: inicioScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan.inicio:not(:hover) {
  animation-name: finScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan.negocio {
  order: -1;
  background-image: url("../img/icono_negocio.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
  background-color: #84d9bc;
}
@media (min-width: 768px) {
  .nuestros-planes .plan.negocio {
    order: unset;
    margin-top: -3rem;
    margin-bottom: -3rem;
    border-radius: 2rem;
  }
}
.nuestros-planes .plan.negocio:hover {
  animation-name: inicioScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan.negocio:not(:hover) {
  animation-name: finScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan.pro {
  background-image: url("../img/icono_pro.svg");
  background-repeat: no-repeat;
  background-position: 50% 5rem;
  background-size: 10rem;
}
@media (min-width: 768px) {
  .nuestros-planes .plan.pro {
    border-radius: 2rem;
  }
}
.nuestros-planes .plan.pro:hover {
  animation-name: inicioScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan.pro:not(:hover) {
  animation-name: finScale;
  animation-duration: 1s;
  animation-timing-function: ease;
  animation-fill-mode: forwards;
}
.nuestros-planes .plan .precio {
  font-size: 1.8rem;
  text-align: center;
  font-weight: 700;
  text-transform: uppercase;
}
.nuestros-planes .plan .listado li {
  text-align: center;
  margin-bottom: 2rem;
}
.nuestros-planes .plan .listado li:last-of-type {
  margin-bottom: 0trm;
}
.nuestros-planes .plan .boton-plan {
  margin: 5rem auto;
  width: 80%;
  display: block;
  background-color: #84d9bc;
  padding: 1rem 3rem;
  color: #313c4e;
  font-weight: bold;
  text-align: center;
}
.nuestros-planes .plan.negocio .boton-plan {
  border: 5px solid #FFFFFF;
}

@keyframes inicioScale {
  0% {
    transform: scale(1);
  }
  100% {
    transform: scale(1.05);
  }
}
@keyframes finScale {
  0% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}
.testimoniales {
  margin: 5rem auto;
  display: flex;
  flex-direction: column-reverse;
}
@media (min-width: 768px) {
  .testimoniales {
    display: grid;
    gap: 4rem;
    grid-template-columns: repeat(5, 1fr);
    align-items: center;
  }
}
@media (min-width: 768px) {
  .testimoniales .imagen {
    grid-column: 1/3;
  }
}
@media (min-width: 768px) {
  .testimoniales .contenidoTestimoniales {
    grid-column: 3/6;
    grid-row: 1/2;
  }
}
.testimoniales .contenidoTestimoniales h2 {
  border-bottom: 5px solid #84d9bc;
  display: block;
}
@media (min-width: 768px) {
  .testimoniales .contenidoTestimoniales h2 {
    display: inline;
  }
}
.testimoniales .contenidoTestimoniales .testimonial {
  padding: 2rem;
  background-color: #84d9bc;
  border: 0.2rem solid #313c4e;
  border-radius: 1.5rem;
  margin-bottom: 4rem;
  position: relative;
}
@media (min-width: 768px) {
  .testimoniales .contenidoTestimoniales .testimonial {
    max-width: 70%;
  }
  .testimoniales .contenidoTestimoniales .testimonial:nth-child(3) {
    margin-left: 10%;
  }
  .testimoniales .contenidoTestimoniales .testimonial:nth-child(4) {
    margin-left: 20%;
  }
}
.testimoniales .contenidoTestimoniales .testimonial::before {
  content: "";
  display: block;
  width: 5rem;
  height: 5rem;
  background-image: url("../img/comilla.svg");
  background-repeat: no-repeat;
  background-position: center;
  position: absolute;
}
.testimoniales .contenidoTestimoniales .testimonial:first-of-type {
  margin-top: 4rem;
}
.testimoniales .contenidoTestimoniales .testimonial p {
  margin: 0;
  padding-left: 6rem;
}

.footer {
  background-color: #313c4e;
  padding-top: 5rem;
  padding: 5rem 1rem;
}
@media (min-width: 768px) {
  .footer {
    padding: 5rem 0;
  }
}
.footer .footer-grid {
  display: grid;
  grid-template-rows: repeat(4, 1fr);
  grid-template-columns: 1fr;
  row-gap: 1rem;
}
@media (min-width: 768px) {
  .footer .footer-grid {
    grid-template-rows: 1fr;
    grid-template-columns: repeat(4, 1fr);
    row-gap: 0rem;
    column-gap: 5rem;
    align-items: center;
  }
}
.footer .footer-grid .footer-widget {
  display: flex;
  flex-direction: column;
  align-items: center;
}
@media (min-width: 768px) {
  .footer .footer-grid .footer-widget {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
  }
}
.footer .footer-grid .footer-widget nav {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 1rem;
}
@media (min-width: 768px) {
  .footer .footer-grid .footer-widget nav {
    display: flex;
    flex-direction: column;
    align-items: flex-start;
    gap: 1rem;
  }
}
.footer .footer-grid .footer-widget nav a {
  text-decoration: none;
  color: white;
}
.footer .copyright {
  margin-top: 5rem;
  text-align: center;
  font-size: 2rem;
}